import { Component, OnInit, ViewChild } from '@angular/core';
import { Task } from 'src/app/class/task';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialog } from '@angular/material/dialog';
import { TaskListModalComponent } from '../task-list-modal/task-list-modal.component';
import { HttpClient, HttpParams } from '@angular/common/http';
import { RootscopeService } from 'src/app/services/rootscope.service';
import { Subscription } from 'rxjs';
import { element } from 'protractor';

@Component({
  selector: 'app-task-list-data',
  templateUrl: './task-list-data.component.html',
  styleUrls: ['./task-list-data.component.css']
})
export class TaskListDataComponent implements OnInit {
  displayedColumns: string[] = ['task_id', 'due_date', 'description', 'Actions'];
  // ELEMENT_DATA: Task[] = [
  //   { position: 1, name: 'Hydrogen', weight: 1.0079, symbol: 'H' },
  //   { position: 2, name: 'Helium', weight: 4.0026, symbol: 'He' },
  //   { position: 3, name: 'Lithium', weight: 6.941, symbol: 'Li' },
  //   { position: 4, name: 'Beryllium', weight: 9.0122, symbol: 'Be' },
  //   { position: 5, name: 'Boron', weight: 10.811, symbol: 'B' },
  //   { position: 6, name: 'Carbon', weight: 12.0107, symbol: 'C' },
  //   { position: 7, name: 'Nitrogen', weight: 14.0067, symbol: 'N' },
  //   { position: 8, name: 'Oxygen', weight: 15.9994, symbol: 'O' },
  //   { position: 9, name: 'Fluorine', weight: 18.9984, symbol: 'F' },
  //   { position: 10, name: 'Neon', weight: 20.1797, symbol: 'Ne' },
  //   { position: 11, name: 'Sodium', weight: 22.9897, symbol: 'Na' },
  //   { position: 12, name: 'Magnesium', weight: 24.305, symbol: 'Mg' },
  //   { position: 13, name: 'Aluminum', weight: 26.9815, symbol: 'Al' },
  //   { position: 14, name: 'Silicon', weight: 28.0855, symbol: 'Si' },
  //   { position: 15, name: 'Phosphorus', weight: 30.9738, symbol: 'P' },
  //   { position: 16, name: 'Sulfur', weight: 32.065, symbol: 'S' },
  //   { position: 17, name: 'Chlorine', weight: 35.453, symbol: 'Cl' },
  //   { position: 18, name: 'Argon', weight: 39.948, symbol: 'Ar' },
  //   { position: 19, name: 'Potassium', weight: 39.0983, symbol: 'K' },
  //   { position: 20, name: 'Calcium', weight: 40.078, symbol: 'Ca' },
  // ];
  ELEMENT_DATA: Task[] = [];
  dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
  addTaskSubscription: Subscription;
  updateTaskSubscription: Subscription;

  constructor(public dialog: MatDialog, private httpService: HttpClient, private R: RootscopeService) { }

  openDialog(from, rowData?) {
    if (from === 'add') {
      let obj = {
        add: true,
      }
      this.R.taskListSubscription.next(obj)
    } else if (from === 'edit') {
      let obj = {
        edit: true,
        data: rowData
      }
      this.R.taskListSubscription.next(obj)
    } else if (from === 'delete') {
      this.deleteTask(rowData);
      return;
    }
    const dialogRef = this.dialog.open(TaskListModalComponent, {
      width: '500px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
  startComponent = () => {
    this.getTaskLists();
  }

  getTaskLists = () => {
    this.dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
    let url = 'http://10.10.30.229:3000/getTask'
    this.httpService.get(url).subscribe(res => {
      let data: any = res;

      if (data && data.message && data.message.length > 0) {
        this.ELEMENT_DATA = data.message;
        if (this.ELEMENT_DATA.length > 0) {
          this.ELEMENT_DATA.filter(obj => {
            if (obj && obj.due_date) {
              obj.due_date = obj.due_date.substring(0, 10);
            }
          })
        }
        this.dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
      }
    })
  }

  addTask = (task) => {
    let url = 'http://10.10.30.229:3000/createTask'
    if (task && !task.task_id) {
      task.task_id = Math.floor((Math.random() * 1000000) + 1);
    }
    this.httpService.post(url, task).subscribe(res => {
      let resData: any = res;
      if (resData && resData.message && resData.message.length > 0) {
        let data: any = res;
        this.ELEMENT_DATA.push(task);
        this.dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
      }
    })
  }

  updateTask = (task) => {
    let url = 'http://10.10.30.229:3000/editTask'
    // if (task && !task.task_id) {
    //   task.task_id = Math.floor((Math.random() * 10) + 1);
    // }
    this.httpService.post(url, task).subscribe(res => {
      let resData: any = res;
      if (resData && resData.message && resData.message.length > 0) {
        this.getTaskLists();
        // let data: any = res;
        // this.ELEMENT_DATA.push(task);
        // this.dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
      }
    })
  }
  deleteTask = (task) => {
    let url = 'http://10.10.30.229:3000/deleteTask'
    // if (task && !task.task_id) {
    //   task.task_id = Math.floor((Math.random() * 10) + 1);
    // }
    this.httpService.post(url, task).subscribe(res => {
      let resData: any = res;
      if (resData && resData.message && resData.message.length > 0) {
        this.getTaskLists();
        // let data: any = res;
        // this.ELEMENT_DATA.push(task);
        // this.dataSource = new MatTableDataSource<Task>(this.ELEMENT_DATA);
      }
    })
  }

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  ngOnInit() {
    this.addTaskSubscription = this.R.addTaskSubscription.subscribe(event => {
      if (event && Object.keys(event).length > 0) {
        this.addTask(event);
      }
    })
    this.updateTaskSubscription = this.R.addTaskSubscription.subscribe(event => {
      if (event && Object.keys(event).length > 0) {
        this.updateTask(event);
      }
    })
    this.dataSource.paginator = this.paginator;
    this.startComponent();
  }

  ngOnDestroy() {
    this.addTaskSubscription && this.addTaskSubscription.unsubscribe();
    this.updateTaskSubscription && this.updateTaskSubscription.unsubscribe();
  }

}
